import React, {useState} from 'react';

import {Nav, Navbar} from 'react-bootstrap';
import { useHistory, useLocation } from "react-router-dom";
import {Navigation} from 'react-minimal-side-navigation';
import 'react-minimal-side-navigation/lib/ReactMinimalSideNavigation.css';
import imgLogo from '../../img/logo.png';

const Menu = (props) => {
    const history = useHistory();
    const location = useLocation();
    const [isSidebarOpen, setIsSidebarOpen] = useState(true);
    
    return (
      <>
        <div className='logo'>
          <img 
            src={ imgLogo }
            width='50'
            height='50'
            alt=''
          />
        </div>
        <Navigation
          // you can use your own router's api to get pathname
          activeItemId={location.pathname}
          onSelect={({ itemId }) => {
            if (itemId==="http://13.124.141.28:9090/") window.open("http://13.124.141.28:9090/", "_blank")
            else history.push(itemId);
          }}
          items={[
            {
              title: '',
              itemId: '/home'
            },
            {
              title: '',
              itemId: '/workerManage',
              subNav: [
                {
                  title: '',
                  itemId: '/workerManage',
                },
                // {
                //   title: '()',
                //   itemId: '/workerManage/contract',
                // },
                {
                  title: ' ',
                  itemId: '/workerManage/staff',
                },
              ],
            },
            {
              title: '',
              itemId: '/taskManage',
              subNav: [
                {
                  title: '',
                  itemId: '/taskManage',
                },
              ],
            },
            {
              title: '',
              itemId: '/payManage',
              subNav: [
                {
                  title: '',
                  itemId: '/payManage',
                },
                {
                  title: '',
                  itemId: '/payManage/extra',
                },
              ],
            },
            {
              title: ' ',
              itemId: '/payDocument',
              subNav: [
                {
                  title: '',
                  itemId: '/payDocument',
                },
                {
                  title: '',
                  itemId: '/payDocument/details',
                },
              ],
            },
            {
              title: '',
              itemId: '/message',
              subNav: [
                {
                  title: ' ',
                  itemId: '/message',
                },
                {
                  title: ' ',
                  itemId: '/message/sended',
                },
                // {
                //   title: ' ',
                //   itemId: '/message/sendMail',
                // }
              ],
            },
            {
              title: '',
              itemId: '/library'
            },
            {
              title: '  ',
              itemId: 'http://13.124.141.28:9090/',
            },
          ]}
        />
      </>
    );
};

export default Menu;
